import { ADD_SUBSCRIBE_EMAIL } from "@/api/api";
export default {
  name: "subscribe",

  data() {
    return {
      subscribeEmail: ''
    };
  },

  methods: {
    subscribe() {
      let params = {
        email: this.subscribeEmail
      };
      ADD_SUBSCRIBE_EMAIL(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.$message.success('Subscribe Success');
          this.subscribeEmail = '';
        }
      }).catch(err => {
        console.log(err);
        this.$message.error(err.msg);
      });
    }

  }
};